/**
 * Styles for front-end and editor
 */

 .wp-block-hs-blocks-accordion-pane > .pane-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    transition: background-color var(--wp--custom--accordion--transition-time) ease-out;
    color: var(--wp--custom--accordion--header--text-color);
    background-color: var(--wp--custom--accordion--header--background-color);
}

.wp-block-hs-blocks-accordion-pane > .pane-header.justify-left {
    justify-content: flex-start;
    gap: 0.5em;

}

.wp-block-hs-blocks-accordion-pane.active > .pane-header {
    color: var(--wp--custom--accordion--active--header--text-color);
    background-color: var(--wp--custom--accordion--active--header--background-color);
}

.wp-block-hs-blocks-accordion-pane .pane-header h2 {
    margin: 0;
    font-size: 1.5em;
}

.wp-block-hs-blocks-accordion-pane .pane-content {
    max-height: 0;
    overflow: hidden;
    transition: max-height var(--wp--custom--accordion--transition-time) ease-out;
}

.wp-block-hs-blocks-accordion-pane.active > .pane-content {
    max-height: unset;
}

.wp-block-hs-blocks-accordion-pane .pane-header .icon {
    cursor: pointer;
}

.wp-block-hs-blocks-accordion-pane .rotating-90, .wp-block-hs-blocks-accordion-pane .rotating-180 {
    transition: transform var(--wp--custom--accordion--transition-time) ease-out;
}

.wp-block-hs-blocks-accordion-pane .rotating-90 {
    transform: rotate(-90deg);
}

.wp-block-hs-blocks-accordion-pane.active > .pane-header > .rotating-90 {
    transform: rotate(0deg);
}

.wp-block-hs-blocks-accordion-pane.active > .pane-header > .rotating-180 {
    transform: rotate(-180deg);
}

.wp-block-hs-blocks-accordion-pane.active > .pane-header > .icon.plus, .wp-block-hs-blocks-accordion-pane > .pane-header > .icon.minus {
    display: none;
}

.wp-block-hs-blocks-accordion-pane.active > .pane-header > .icon.minus, .wp-block-hs-blocks-accordion-pane > .pane-header > .icon.plus {
    display: block;
}


/* See 9.4 / 9.5 of https://drafts.csswg.org/selectors-4/#the-focus-visible-pseudo */

.wp-block-hs-blocks-accordion-pane .pane-header:focus {
    outline: none;
}

.wp-block-hs-blocks-accordion-pane .pane-header:focus-visible {
    outline-width: 2px;
    outline-style: solid;
    outline-color: #000000; 
}

